* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	scroll-behavior: smooth;
}
h1,
h2,
h3,
h4,
h5,
h6 {
	margin-bottom: 0;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}
img {
	width: 100%;
}
p {
	margin-bottom: 0 !important;
}
::-webkit-scrollbar {
	width: 15px;
}
::-webkit-scrollbar-thumb {
	background: linear-gradient(to bottom, #8dc645, #1976bc);
	-webkit-transition: all 0.3s;
	transition: all 0.3s;
	border-radius: 10px;
}
/* NAVBAR COMPONENT */
.logo {
	width: 150px;
}
.nav-item .nav-link {
	color: #000;
	font-weight: bold;
	font-size: 1.2rem;
	transition: all 0.3s;
	margin: 0 10px;
}
.nav-item .nav-link:hover,
.nav-item .nav-link.active {
	box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.5);
	border-bottom: 3px solid #000;
}
/* HEADER COMPONENT */
.header {
	position: relative;
}
.slider-img {
	width: 100%;
	height: 855px;
}
.slider-img::after {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 760px;
	background: rgba(0, 0, 0, 0.4);
}
.slider-img img {
	width: 100%;
}
.slider-content {
	position: absolute;
	top: 40%;
	width: 100%;
	text-align: center;
	color: #fff;
	text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
}
.discover-btns a {
	padding: 1em;
	border-radius: 0;
	border: 0;
	font-weight: 600;
}
.discover-btns a:first-child {
	background-color: #8dc645;
	margin-right: 10px;
}
.discover-btns a:last-child {
	background-color: #1976bc;
}
.slider-content .slogan {
	font-size: 1.5rem;
	font-weight: bold;
	margin-bottom: 20px;
	position: relative;
}
.slider-content .slogan:after {
	content: "";
	position: absolute;
	bottom: -10px;
	left: 50%;
	transform: translateX(-50%);
	display: block;
	width: 8%;
	border-bottom: 3px solid #fff;
}
.slider-content h1 {
	font-size: 3rem;
	margin-bottom: 20px;
}
.slider-content span {
	margin-left: 10px;
	font-weight: 300;
}
.slick-prev {
	left: 50px;
	z-index: 1;
}
.slick-next {
	right: 50px;
}
.slick-arrow {
	width: 100px;
	height: 100px;
}
.slick-prev:before,
.slick-next:before {
	font-size: 50px;
}
/* TOURS COMPONENT */
.tours {
	background-color: rgb(255, 255, 255);
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
	padding: 35px;
	width: 80%;
	margin: 0 auto;
	position: absolute;
	bottom: -300px;
	left: 0;
	right: 0;
	text-align: center;
	border-bottom: 5px solid #1976bc;
}
.tours-form {
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	margin-top: 20px;
}
select {
	border: none;
	border-bottom: 2px solid #000;
	color: #8dc645;
	outline: none;
	box-shadow: 0 0 10px #1976bc;
	padding: 5px;
	border-radius: 5px;
	width: 250px;
}
option {
	background-color: #1976bc;
	padding: 20px 0;
	border: none;
	font-size: 20px;
	font-weight: 600;
	color: #8dc645;
}
.tours-form-group input {
	border: none;
	width: auto;
	border-bottom: 2px solid #000;
	outline: none;
	box-shadow: 0 0 10px #1976bc;
}
.tours-form-group input:last-child {
	border-bottom: none;
}
/* GREETING COMPONENT */
.greeting {
	padding: 50px 0;
}
.greeting-text,
.religion-text {
	text-align: center;
	margin-bottom: 40px;
}
.greeting-text p,
.religion-text p {
	font-size: 1rem;
	font-weight: bold;
	margin-bottom: 10px;
}
.greeting-text h2 {
	font-size: 4rem;
	font-weight: bold;
	margin-bottom: 20px;
	position: relative;
}
.greeting-text h2::after {
	content: "";
	position: absolute;
	bottom: 5px;
	left: 50%;
	transform: translateX(-50%);
	width: 23%;
	height: 20px;
	background: linear-gradient(to right, #8dc645, #1976bc);
	z-index: -1;
	opacity: 0.8;
}
.greeting-text p:last-of-type {
	width: 60%;
	margin: 0 auto;
}
.greeting-item {
	margin-bottom: 40px;
}
.box {
	transition: 0.3s all;
}
.box:hover {
	transform: translateY(-20px);
}
.greeting-img,
.religion-img {
	position: relative;
}
.greeting-img::before,
.religion-img::before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	transition: 0.3s all;
}
.box:hover .greeting-img::before,
.religion-item:hover .religion-img::before {
	background: rgba(0, 0, 0, 0.3);
}
.price-label {
	position: absolute;
	top: 20px;
	background-color: #1976bc;
	padding: 10px 20px;
	left: -10px;
	z-index: 2;
}
.price-label h3 {
	margin-bottom: 0;
	color: #fff;
	font-size: 15px;
}
.greeting-content {
	padding: 10px;
	background-color: #fff;
	box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
}
.greeting-content a {
	text-decoration: none;
	color: #000;
}
.greeting-content h4 {
	font-size: 23px;
	font-weight: bold;
}
.tags {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	padding-bottom: 15px;
	border-bottom: 1px solid #000;
}
.tags span {
	margin-right: 10px;
	text-decoration: underline;
}
.reviews {
	margin: 15px;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}
.review-count span {
	padding-right: 3px;
}
.duration i {
	padding-right: 6px;
}
/* RELIGIOUS COMPONENT */
.religion {
	padding-bottom: 100px;
}
.rel-box {
	transition: 0.4s all;
}
.religion h2 {
	font-size: 3.5rem;
	font-weight: bold;
	margin-bottom: 0;
}
.religion-span {
	font-size: 4rem;
	font-weight: bolder;
	position: relative;
	margin-right: 10px;
}
.religion-span::after,
.features-title strong::after {
	content: "";
	position: absolute;
	bottom: 0;
	left: 0;
	display: block;
	width: 100%;
	height: 20px;
	background: linear-gradient(to right, #8dc645, #1976bc);
	z-index: -1;
	opacity: 0.8;
}
.religion-text {
	margin-bottom: 40px;
}
.religion-text p:last-of-type {
	width: 60%;
	margin: 30px auto 0;
}
.religion-title {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	transition: 0.4s all;
	opacity: 0;
}
.rel-box:hover .religion-title {
	opacity: 1;
}
.religion-title h1 {
	color: #fff;
	font-weight: bolder;
	font-size: 60px;
	font-family: poppins;
}
/* FEATURES COMPONENT */
.features {
	text-align: center;
	padding-bottom: 50px;
}
.features-txt {
	margin-bottom: 40px;
	padding-bottom: 50px;
	border-bottom: 2px solid #333;
}
.features-txt p {
	font-size: 1.2rem;
	font-weight: bold;
	margin-bottom: 10px;
	width: 80%;
	margin: 0 auto;
}
.features-title {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-bottom: 25px;
}
.features-title h2 {
	font-size: 3rem;
	font-weight: bold;
	margin-bottom: 0;
}
.features-title strong {
	font-size: 2.5rem;
	font-weight: bold;
	position: relative;
}
.slider-icon i {
	font-size: 3rem;
	color: #8dc645;
	margin-right: 10px;
}
.slider-txt p {
	font-size: 25px;
	font-weight: bold;
}
.slider-txt p:first-child {
	font-size: 14px;
	font-weight: 100;
}
.slider-icon img {
	width: 40px;
	margin: 0 auto;
	padding-bottom: 5px;
}
.find-tour-btn {
	text-align: center;
	margin-top: 70px;
}
.find-btn {
	text-shadow: 0 1px 3px rgb(24 24 24 / 15%);
	color: #fff !important;
	background: #1976bc !important;
	padding: 15px !important;
	border-radius: 0 !important;
	transition: 0.8s all !important;
	border: none;
}
.find-btn:hover {
	background: linear-gradient(to bottom, #1976bc 0%, #8dc645 100%) !important;
}
/* REVIEWS COMPONENT */
.review {
	margin-bottom: 50px;
}
.solid-bg {
	background-color: #1976bc;
	padding: 10px;
	width: 100%;
	height: 50vh;
	display: flex;
	justify-content: center;
	align-items: center;
}
.solid-bg p {
	color: #fff;
	font-size: 5rem;
	font-weight: bold;
	user-select: none;
}
.review-txt {
	text-align: center;
	margin-bottom: 100px;
}
.review-title strong {
	font-size: 3rem;
	position: relative;
}
.review-title strong::after {
	content: "";
	position: absolute;
	bottom: 0;
	left: 0;
	display: block;
	width: 100%;
	height: 20px;
	background: linear-gradient(to right, #8dc645, #1976bc);
	z-index: -1;
	opacity: 0.8;
}
.review-title span {
	padding-left: 5px;
	font-size: 3rem;
}
.review-slide .slider-img {
	height: 100%;
}
.review-slide .slider-img img {
	margin: auto 0;
}
.review-slide .slider-img::after {
	display: none;
}
/* SEARCH COPONENT */
.search {
	padding: 160px 0;
	width: 100%;
	height: 75vh;
	background: linear-gradient(to bottom, #1976bc 0%, #8dc645 100%);
}
.search-box {
	margin: 0 auto;
}
.search-box input {
	width: 100%;
	height: 100px;
	margin: 0 auto;
	border: 2px solid #000;
	font-size: 1.5rem;
	font-weight: bold;
	color: #fff;
	outline: none;
	text-align: center;
}
/* FOOTER COMPONENT */
footer {
	background-color: #181818;
	padding: 15px;
}
footer p {
	color: #fff;
	font-size: 20px;
	text-align: center;
	margin-bottom: 0;
}
footer p span {
	padding: 0 10px;
}
footer p a {
	color: #fff;
	text-decoration: none;
	transition: 0.3s all;
}
footer p a:hover {
	color: cyan;
	font-weight: bold;
}
